    //        
    const orderBlank = document.getElementById("order-blank");
    orderBlank.innerHTML = "";

    //    
    const categories = {};

    orderData.menuItems.forEach((item) => {
      const category = item.category || " "; //    ,  " "

      if (!categories[category]) {
        categories[category] = [];
      }

      categories[category].push(item);
    });

